<template>
  <div id="app">
    <!-- 图片作为装饰，放在左上角 -->
    <div style="position: absolute; top: 10px; left: 10px;">
      <img src="@/assets/logo.png" alt="刷新数据" style="width: 24px; height: 24px;">
    </div>

    <!-- 刷新按钮，与图片并排 -->
    <div style="position: absolute; top: 10px; left: 40px;">
      <el-button type="text" @click="fetchData">
        刷新数据
      </el-button>
    </div>

    <!-- 表格内容 -->
    <el-table :data="tableData" style="width: 100%; margin-top: 60px;">

      <el-table-column prop="created_at" label="入校时间" width="180"></el-table-column>
      <el-table-column prop="name" label="姓名" width="180"></el-table-column>
      <el-table-column prop="nameid" label="身份证" width="180"></el-table-column>
      <el-table-column prop="carid" label="车牌" width="180"></el-table-column>
   
    </el-table>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { fetchDataFromApi } from './api/dataService';

export default {
  setup() {
    const tableData = ref([]);

    const fetchData = () => {
      fetchDataFromApi()
        .then(data => {
          tableData.value = data.map(item => ({
            ...item,
            created_at: new Date(item.created_at).toLocaleString(),
          }));
        })
        .catch(error => {
          console.error('获取数据失败:', error);
        });
    };

    onMounted(() => {
      fetchData();
    });

    return {
      tableData,
      fetchData,
    };
  },
};
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>